body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
    background-color: hsl(218, 22%, 67%);
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
}
.App{
    width: 100%;
}
.sign-up{
    margin: auto;
    width: 70%;
    background-color: white;
    padding: 2rem;
    border-radius: 10px;
    box-shadow: 2px 2px 10px 1px hsl(178, 63%, 7%);
}
h1{
    text-align: center;
    color: hsl(71, 50%, 46%);
}
h2{
    text-align: center;
    color: hsl(179, 59%, 18%);
}
input, select{
    margin: .8em 0;
    padding: .5rem;
    border-radius: 5px;
    margin-left: 5px;
    border: 1px solid hsl(179, 59%, 18%);
    cursor: pointer;
    width: 90%;
}
select{
    outline: none;
}
button{
    padding: .8rem;
    color: white;
    font-weight: 700;
    border: 1px solid transparent;
    background-color: hsl(178, 63%, 7%);
    display: flex;
    justify-content: center;
    width: 150px;
    border-radius: 10px;
    margin: .5rem auto;
    transition: 0.5s ease;
    cursor: pointer;
}
button:hover{
    color: hsl(178, 63%, 7%);
    background-color: white;
    border: 1px solid hsl(178, 63%, 7%);
}
p{
    color: hsl(179, 59%, 18%);
}
.message{
    color: red;
    font-weight: 600;
    font-size: .8rem;
}
.sign{
    text-decoration: underline;
    cursor: pointer;
}
