.sign-in{
    margin: auto;
    width: 70%;
    background-color: white;
    padding: 2rem;
    border-radius: 10px;
    box-shadow: 2px 2px 10px 1px hsl(178, 63%, 7%);
}
h1{
    text-align: center;
    color: hsl(71, 50%, 46%);
}
h2{
    text-align: center;
    color: hsl(179, 59%, 18%);
}
input{
    margin: .8em 0;
    padding: .5rem;
    border-radius: 5px;
    margin-left: 5px;
    border: 1px solid hsl(179, 59%, 18%);
    cursor: pointer;
}
button{
    padding: .8rem;
    color: white;
    font-weight: 700;
    border: 1px solid transparent;
    background-color: hsl(178, 63%, 7%);
    display: flex;
    justify-content: center;
    width: 150px;
    border-radius: 10px;
    margin: .5rem auto;
    transition: 0.5s ease;
    cursor: pointer;
}
button:hover{
    color: hsl(178, 63%, 7%);
    background-color: white;
    border: 1px solid hsl(178, 63%, 7%);
}
p{
    color: hsl(179, 59%, 18%);
}
.sign{
    text-decoration: underline;
    cursor: pointer;
}
.message{
    color: red;
    font-weight: 600;
    font-size: .8rem;
}